import React from 'react';
import Cards from './cards';

// קומפוננטה שמקבלת ומציגה מידע
class BITprice extends React.Component {
    constructor(props) {
        super(props);
            this.state = {
                loading: true,
                price: [],

            };
    }
    // DOM-פונקציה שמציבה את הקומפוננטה ב 
    // בתוכה יש פונקציה שתפעיל 2 פונקציות כל 5 שניות
    async componentDidMount() {
        setInterval( () => {
            this.getPrice();
        
            }, 5000)
    }
    async getPrice() {
        const url= 'https://api.coinmarketcap.com/v2/ticker/?limit=18'
        const response = await fetch(url)
        const results = await response.json()
        this.setState({price: results, loading: false});
        console.log(results.data);
    }

    renderPrices() {
        return this.state.price.map((price) => (
            <Cards key={price.url} price={price} />
        ));
    }

    render() {
        return (

        <div className="App">
            {/* ל-סטייט this הכוונה של  
             נכון אז תציג הודעה ראשונה loadin ושואל (?), אם 
             ואם לא (:), אז תציג את ההודעה השניה */}
            {/* {this.state.loading ? <div>loading data...</div> : <div>price</div>} */}

            {this.state.loading || !this.state.price ? (
                <div>loading data...</div>
                ) : (
                    <div>
                        <div>{'$'+ this.state.price.data[1].quotes.USD.price.toFixed(2)}</div>
                    </div>
                )}
        </div>
    );
  }
}
    
export default BITprice;

